<template>
  <div class="login">
    <el-row class="flex">
      <el-col :span="8" :offset="8" class="login-box">
        <h3>小U商城管理后台</h3>
        <el-form :model="forminfo" :rules="rules" ref="form">
          <el-form-item prop="username">
            <el-input placeholder="请输入账号" v-model="forminfo.username">
              <template slot="prepend">
                <i class="el-icon-user"></i>
              </template>
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input placeholder="请输入密码" show-password v-model="forminfo.password">
              <template slot="prepend">
                <i class="el-icon-lock"></i>
              </template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="btn" @click="submit">立即登录</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { login } from "@/request/admin"
export default {
    data(){
        return{
          forminfo:{
            username:"武哥",
            password:"admin888"
          },
          rules: {
            username: [{ required: true, message: "请输入账号", trigger: "blur" }],
            password: [{ required: true, message: "请输入密码", trigger: "blur" }]
          }
        }
    },
    created(){},
    methods:{
      submit() {
        this.$refs.form.validate((valid) => {
          if (valid) {
            login(this.forminfo)
          } else {
            this.$message.warning("验证失败");
          }
        });
      }  
    },
    components:{}
}

</script>
<style scoped>
.login {
  width: 100vw;
  height: 100vh;
  background-image: url("../assets/loginbg.jpeg");
  background-repeat: no-repeat;
  background-position: center;
}
.flex {
  display: flex;
  align-items: center;
  height: 100vh;
}
.login-box {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px 70px;
}
.login-box h3 {
  color: orange;
  text-align: center;
  letter-spacing: 2px;
  margin: 10px 0 30px;
}
.login-box .btn {
  display: block !important;
  width: 100%;
}
</style>


